import { LMap, LTileLayer, LMarker } from 'vue2-leaflet';

export default {
    mounted () {
        console.log(this.$attrs)
    },
  components: {
    LMap,
    LTileLayer,
  },
  props: {
    url: {
      type: String,
    },
    zoom: {
      type: Number,
    },
    center: {
      type: Array,
    },
    classCSS: {
      type: String,
    },
    bounds: Object,
  },
  methods: {
    zoomUpdated (zoom) {
      this.zoom = zoom;
    },
    centerUpdated (center) {
      this.center = center;
    },
    boundsUpdated (bounds) {
      this.bounds = bounds;
    }
  }
};
